.item {
    @apply border-2 border-transparent w-10 h-10 rounded-md bg-gray-300 flex items-center justify-center text-2xl
}

.finish-btn {
    @apply mt-4 bg-gray-300 text-center h-10 flex items-center justify-center text-2xl rounded-md
}

.item.selected {
    @apply bg-primary-100 text-white
}

.item.current {
    @apply border-primary-100
}

.enabled {
    @apply bg-green-500 text-white font-semibold
}

.item.current.selected {
    @apply bg-blue-500
}